@import '../../assets/css/variable.less';
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 72px;

  .menu {  
    display: flex;
    transform: inherit;
  }

  .left {
    display: flex;
    align-items: center;
    .logo {
      margin-left : 48px;
      display: flex;
      align-items: center;
      > a {
        color: @whiteFontColor;
        text-decoration: none; 
        display: flex;
      }
      .name {    
        margin: auto 12px;
        color: @whiteFontColor;
        font-size: @20FontSize;
      }
    }
  }
  .right {
    display: flex;
    margin-right: 24px;
  }
  .small-menu {
    margin-left: 24px;
  }

}
